import { useState } from "react";
import styled from "styled-components";

const StyledButton = styled.button`
  color: ${(p)=>p.color || 'black'};
  background-color: ${(p)=>p.background || 'white'};
  border: 1px solid red;
`;

function NumberList(){
    //배열 - [1,2,3,4,5] 변수 선언
    //1. 배열의 내용 출력 - <ul><li> 태그
    //2. 값 1개 입력 text타입 input
    //3. button 3개 -  추가, 수정, 삭제 - Styled 형태 사용
    //값 입력하고 추가 버튼 클릭시 배열 마지막 요소 추가
    //값 입력하고 수정 버튼 클릭시 배열 마지막 요소 수정
    //삭제 버튼 클릭시 배열 마지막 요소 삭제
    // 버튼 클릭하여 해당 작업 수행 결과 화면에 즉각 반영
  const [array, setArray] = useState([1, 2, 3, 4, 5]);
  const [input, setInput] = useState('');
  // const print = <ul>{array.map((item) => {
  //   return <li>{item}</li>;
  // })}</ul>;
  return <div>
    <ul>
      {array != undefined ? array.map((item) => <li>{item}</li>) : ''}
    </ul>
    <StyledButton onClick={() => setArray([...array, input])}>추가</StyledButton>
    <StyledButton onClick={() => setArray([...array.slice(0, -1), input])}>수정</StyledButton>
    <StyledButton onClick={() => setArray(array.slice(0, -1))}>삭제</StyledButton>
    <br />
    값 <input type="text" value={input} onChange={(e) => setInput(e.target.value)} />
  </div>;
}

export default NumberList;