import React, { useState } from "react";

class State_User1 extends React.Component{
  constructor(){
    super();
    this.state = {id:"id의 초기값", name:"name의 초기값"};
    this.dept = "dept의 초기값";
  }
  
  render(){
    let handlerfunction = () => {
      this.setState({id:"id의 수정값", name:"새로운 name값"});
    };
    return <div>
      <h3>id출력={this.state.id}</h3>
      <h3>name출력={this.state.name}</h3>
      <button onClick={handlerfunction}>변경</button>
      </div>;
  }
}

function State_User2(){
  // let [id, setId] = useState("초기값");
  // let handlerfunction = () => {
  //   setId("새로운 문자열 변경값");
  // };
  // return <div>state변수값={id}<button onClick={handlerfunction}>변경</button></div>
  let [obj, setObj] = useState({id:"id", count:100});
  let handlerfunction = function(){
    setObj({...obj, count:1000});
  };
  return <div>state변수값1={obj.id}, state변수값2={obj.count}<button onClick={handlerfunction}>변경</button></div>
};

export {State_User1, State_User2};