import React from "react";

function Hello1(){
  let msg = '나의 리액트 컴포넌트 생성';
  let msg2 = '(함수선언식)'
  return <div><h1>{msg}</h1><h3>{msg2}</h3></div>;
}

const Hello2 = function(){
  let msg = '나의 리액트 컴포넌트 생성';
  let msg2 = '(익명의 함수)'
  return <div><h1>{msg}</h1><h3>{msg2}</h3></div>;
}

const Hello3 = () => <div><h1>나의 리액트 컴포넌트 생성</h1><h3>(화살표 함수)</h3></div>;

class Hello4 extends React.Component{
  render(){
    return <div><h1>나의 리액트 컴포넌트 생성</h1><h3>(클래스 구조)</h3></div>;
  }
}

export {Hello1 as default, Hello2, Hello3, Hello4};